.todo-list-item {
	font-size: 1.25rem;
}

.todo-list-item button {
	width: 35px;
	margin: 3px;
}

.todo-list-item-label {
	margin-left: 1.25rem;
	line-height: 35px;
	cursor: pointer;
	-webkit-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
}

.todo-list-item .todo-list-item-label {
	text-decoration: line-through;
}

.done {
	text-decoration: line-through;
}

.important {
	font-weight: bold;
	color: steelblue;
}

.todo-list-item .todo-list-item-label {
	font-weight: bold;
	color: steelblue;
}


.todo-list .list-group-item {
	padding: .25rem .75rem;
}
.item-add-form {
	margin-top: 10px;
}

.item-add-form input {
	margin-right: 3px;
}

.main {
	width: 600px;
	display: flex;
	flex-direction: column;
	text-align: center;
	margin: 0 auto;
}
